.center-block {
  &__search {
    display: flex;
    align-items: center;
    margin-bottom: 51px;
    border-bottom: 1px solid $tundora;
    width: 100%;
  }

  &__title {
    @include text($stratos, $font-weight-standard, $font-size-title, $white, $line-height-standard);

    margin-bottom: 45px;
    letter-spacing: -0.8px;

    &.skeleton {
      width: 100%;
      height: 71px;
      max-width: 100px;
      background: $mine-shaft;
    }
  }

  &__filter {
    display: flex;
    align-items: center;
    margin-bottom: 51px;
    gap: 15px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
}
